.topHeader {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    // height: 100px;
    background-color: black;
    z-index: 100;
}

.topHeaderContents {
    display: flex;
    position: relative;
    width: 100%;
    max-width: 100%;
    height: 50px;
    color: white;
    background-color: black;
    margin: 0;
    align-items: center;
    justify-content: center;
}

.label {
  position: absolute;
  right: 50px;
}

.button {
  background-color: white;
}


.header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    background-color: #f4f4f4;
    z-index: 100;
  }

  .contents {
    display: flex;
    position: relative;
    width: 100%;
    max-width: 1100px;
    height: 150px;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
  }

  .logo {
    display: flex;
    justify-content: center;
    font-size: 40px;
    line-height: 1;
    width: 100%;
    font-family: "Raleway", "Noto Sans KR", Arial, sans-serif !important;
  }
  
  .navigation {
    ul {
      position: absolute;
      display: flex;
      list-style: none;
      // size: 500px;
      // justify-content: center;
      top: 70px;
      right: 10px;
      // width: 5%;
  
      li + li {
        margin-left: 30px;
      }
    }
  }